import React from 'react';
import AddFish from './AddFish';
import firebase from "firebase";
import PropTypes from 'prop-types';
import base, { app } from '../base';
class Inventory extends React.Component {
	constructor() {
		super();
		this.renderInventory = this.renderInventory.bind(this);
		this.handleChange = this.handleChange.bind(this);
		this.renderLogin = this.renderLogin.bind(this);
		this.authenticate = this.authenticate.bind(this);
		this.authenticateGit = this.authenticateGit.bind(this);
		this.authHandler = this.authHandler.bind(this);
		this.logout = this.logout.bind(this);
		this.state = {
			uid: null,
			owner: null
		}
	}
	componentDidMount() {
		firebase.auth().onAuthStateChanged(user => {
			if (user) {
				this.authHandler({user});
			}
		})
	}
	handleChange(e, key) {
		const fish = this.props.fishes[key];
		// console.log(fish);
		// console.log(e.target);
		const updatedFish = {
			...fish,
			[e.target.name]: e.target.value
			};     //Object.assign({}, fish);
		// console.log(updatedFish)
		this.props.updateFish(key, updatedFish);
	}
	authenticateGit() {
		// console.log(`Using ${provider}`) //
		const authProvider = new firebase.auth.GithubAuthProvider();
		app
		.auth()
		.signInWithPopup(authProvider)
		.then(this.authHandler);
	}
	authenticate = provider => {
		console.log(`Using ${provider}`) //
		const authProvider = new firebase.auth.GithubAuthProvider();
		app
		.auth()
		.signInWithPopup(authProvider)
		.then(this.authHandler);
	}
	async logout() {
		await firebase.auth().signOut();
		this.setState({ uid: null});
	}
	async authHandler(authData) {
		console.log("yohit")
		console.log(authData)
		console.log(this.props.storeId)
		const store = await base.fetch(this.props.storeId, { context: this});
		console.log(store)
		if (!store.owner) {
			await base.post(`${this.props.storeId}/owner`, {
				data: authData.user.uid
			}); 
		}
		this.setState({ 
			uid: authData.user.uid,
			owner: store.owner || authData.user.uid})
		}
		
	renderLogin() {
		return (
			<nav>
				<h2> Inventory </h2>
				<p> Sign in to manage your store's inventory </p>
				<button onClick={() => this.authenticateGit()}> GitHub Login </button>
				<button onClick={() => this.authenticate('email')}> Email Login (not setup yet) </button>
			</nav>
			)
	}
	renderInventory(key) {
		const fish = this.props.fishes[key];
		return (
			<div key={key}>
				<input name="name" value={fish.name} type="text" placeholder="Fish Name"
				onChange={(e) => this.handleChange(e, key)} />
				<input name="price" value={fish.price} type="text" placeholder="Price"
				onChange={(e) => this.handleChange(e, key)} />
				<select name="status" value={fish.status} 
				onChange={(e) => this.handleChange(e, key)}>
					<option value="available">Fresh</option>
					<option value="unavailable">SoldOut</option>
				</select>
				<input name="desc" value={fish.desc} type="textarea" placeholder="Description" 
				onChange={(e) => this.handleChange(e, key)}/>
				<input name="image" value={fish.image} type="text" placeholder="Image" 
				onChange={(e) => this.handleChange(e, key)}/>
				<button onClick={()=> this.props.removeFish(key)}> Remove Fish </button>
				<br />
			</div>


		)
	}
	render() {
		const logout = <button onClick={this.logout}> LogOu </button>;
		if(!this.state.uid) {
			return <div>{this.renderLogin()}</div>
		} 
		if(this.state.uid != this.state.owner) {
			return <div>Sorrey {logout}</div>
		}
		return (
			<div className='stack'>Inventory {logout}
				{Object.keys(this.props.fishes).map(this.renderInventory)}
				<AddFish addFish={this.props.addFish}/>
				<br />
				<button onClick={this.props.loadSamples}> Load Sample Fishes </button>
			</div>
		)
	}
}

Inventory.propTypes = {
	fishes: PropTypes.object.isRequired,
	updateFish: PropTypes.func.isRequired,
	removeFish: PropTypes.func.isRequired,
	addFish: PropTypes.func.isRequired,
	loadSamples: PropTypes.func.isRequired,
	storeId: PropTypes.string.isRequired
}


export default Inventory;