export function rando(arr) {
	console.log('InRando')
	return arr[Math.floor(Math.random() * arr.length)];
}
export function getFunName() {
	console.log('IngetFunNmae')
	var adj = ['blue', 'red', 'gree', 'clean', 'dirty', 'hot'];
	var nouns = ['car', 'leaves', 'tree', 'pillow', 'blanket'];
	return `${rando(adj)}-${rando(nouns)}`;
}