import React from 'react';
import PropTypes from 'prop-types'

class AddFish extends React.Component {
	createFish(event) {
		event.preventDefault();
		console.log("Creating fish now")
		console.log(this.name.value)
		const fish = {
			name: this.name.value,
			price: this.price.value,
			status: this.status.value,
			desc: this.desc.value,
			image: this.image.value,

		}
		console.log(fish)
		this.props.addFish(fish);
		this.fishForm.reset(); // to reset form. check ref on form tag
	}
	render() {
		return (
			<form ref={(input) => this.fishForm = input}className="store-selector" 
				onSubmit={this.createFish.bind(this)}>
				<input ref={(input) => this.name = input} type="text" placeholder="Fish Name"/>
				<input ref={(input) => this.price = input} type="text" placeholder="Price"/>
				<select ref={(input) => this.status = input}>
					<option value="available">Fresh</option>
					<option value="unavailable">SoldOut</option>
				</select>
				<input ref={(input) => this.desc = input} type="textarea" placeholder="Description"/>
				<input ref={(input) => this.image = input} type="text" placeholder="Image"/>
				<button type="submit"> Add Item </button>
			</form>
		)
	}
}
AddFish.propTypes = {
	addFish: PropTypes.func.isRequired
}

export default AddFish;