import React from 'react';
import Header from './Header';
import Order from './Order';
import Inventory from './Inventory.js';
import sampleFishesss from'../sample-fishes';
import Fish from './fish';
import base from '../base';
import {withRouter} from 'react-router-dom';
import PropTypes from 'prop-types'

class App extends React.Component {
	constructor() {
		super();
		this.addFish = this.addFish.bind(this); //binds addFish to app
		this.updateFish = this.updateFish.bind(this);
		this.removeFish = this.removeFish.bind(this);

		// get initial state

		this.loadSamples = this.loadSamples.bind(this);
		this.addToOrder = this.addToOrder.bind(this);
		this.removeFromOrder = this.removeFromOrder.bind(this);
		this.state = {
			fishes: {},
			order: {}

		};
	}
	componentDidMount() {
		

		this.ref = base.syncState(`${this.props.match.params.storeId}/fishes`
			, {
				context: this,
				state: 'fishes'
			});
		const localStorageRef = localStorage.getItem(this.props.match.params.storeId);
		if (localStorageRef) {
			this.setState({order: JSON.parse(localStorageRef) });
		}
	}
	componentDidUpdate() {
		console.log(this.state.order);
		localStorage.setItem(
			this.props.match.params.storeId,
			JSON.stringify(this.state.order)
		);
	}

	// this will unsync the fishes state when you switch stores or something
	compnentWillUnmount() {
		base.removeBinding(this.ref);
	}
	// jugaad for componentWillUpdate
	// static getDerivedStateFromProps(nextProps, nextState) {
	// 	console.log("Something changed")
	// 	console.log(nextProps, nextState)
	// 	// console.log(this.props.match.params.storeId)
		
	// 	localStorage.setItem(`order-${nextProps.match.params.storeId}`,
	// 		JSON.stringify(nextState.order));
	// }

	
	addFish(fish){
		// update state
		// this.state.fishes.fish1 = fish
		console.log('ActualMethodToAddFish')
		const fishes = {...this.state.fishes};
		// above line creates a copy of fishes in the this.state 
		const timestamp = Date.now();
		fishes[`fish-${timestamp}`] = fish

		this.setState({fishes}) // or fishes: fishes (first fish is stateobj)
									// second is const fish def above

	}
	removeFish(key) {
		const fishes = {...this.state.fishes};
		// not delete fishes[key] coz of firebase issue
		fishes[key] = null;
		this.setState({ fishes });
	}
	updateFish(key, updatedFish) {
		const fishes = {...this.state.fishes};
		fishes[key] = updatedFish;
		this.setState({ fishes });
	}
	loadSamples() {
		this.setState({
			fishes: sampleFishesss
		});
	}
	addToOrder(key) {
		// copy of state
		const order = {...this.state.order};
		// update num of fish
		order[key] = order[key] + 1 || 1;
		this.setState({order});
	}
	removeFromOrder(key) {
		const order = {...this.state.order};
		delete order[key];
		this.setState({ order });
	}
	render() {
		console.log("InApp")
		console.log(this)
		// addFish() in inventory below - use props to send something to 
			// children 
		return (

			<div className="catch-of-the-day">
				<div className="menu">
					<Header firstName="Fresh SeaFood"/>
					<div className= "listOfFishes">
						<ul>
							{
								Object
									.keys(this.state.fishes)
									.map(key => <Fish key={key} index={key} 
										details={this.state.fishes[key]} 
										addToOrder={this.addToOrder} />)
							}
						</ul>
					</div>
				</div>
				<Order fishes={this.state.fishes} order={this.state.order} 
					params={this.props.params}
					removeFromOrder={this.removeFromOrder}/>
				<Inventory 
				removeFish={this.removeFish}
				updateFish={this.updateFish} loadSamples={this.loadSamples} addFish={this.addFish}
				fishes={this.state.fishes} storeId={this.props.match.params.storeId}/>
			</div>
		)
	}
}

App.propTypes = {
	params: PropTypes.object.isRequired
}
export default withRouter(App);